import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, ViewController } from 'ionic-angular';
import { AngularFirestore } from 'angularfire2/firestore';
import { Collections } from '../../app/app.models';

@IonicPage()
@Component({
  selector: 'page-asesoria-add',
  templateUrl: 'asesoria-add.html',
})
export class AsesoriaAddPage {

  private addData: any = {name: '', desc: '', url: ''}

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private load: LoadingController,
    public viewCtrl: ViewController,
    private afs: AngularFirestore
  ) {
  }

  ionViewDidLoad() {
    
  }

  async addAsesoria() {

    const l = this.load.create({content: 'Agregando Asesor√≠a...'})
    l.present()

    const id = this.afs.createId()
    await this.afs.doc(`${Collections.LIVESTREAM}/${id}`).set({
      id,
      ...this.addData,
      date: new Date().toISOString()
    })

    l.dismiss()
    this.viewCtrl.dismiss()
    
  }

}



